<template>
    <div id="login">
        <div  class="view_main">
            <span></span>
            <el-input v-model="username" placeholder=""  class="input_type"></el-input>
        </div>
        <div  class="view_main">
            <span></span>
            <el-input type="password" v-model="password" placeholder="" class="input_type"></el-input>
          </div>
        <div  class="view_main">
            <el-button></el-button>
        </div>
    </div>
</template>
    <script>
    export default {
        data() {
        return {
            username: '',
            password:""
        }
        }
        }
    </script>
<style>
.input_type{
    width:200px;
    }
</style>


